/* Основное изображение для кликов */
.click-target {
  width: 70vw; /* Задаем ширину 70% от ширины экрана */
  max-width: 300px; /* Ограничиваем максимальную ширину, чтобы избежать слишком большого увеличения */
  height: auto;
  display: block;
  margin: 0 auto;
}

/* Бонусные изображения - в два раза меньше */
.bonus-item {
  width: 35vw; /* Половина ширины основного изображения */
  max-width: 150px; /* Ограничиваем максимальную ширину бонусов */
  height: auto;
  display: block;
  margin: 0 auto;
}

/* Общие стили для основного контейнера */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #ecf0f1;
  background-color: #2c3e50;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Счетчик очков */
.score {
  font-size: 8vw; /* Увеличиваем размер шрифта для счетчика */
  margin-bottom: 20px;
}
