.start-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #34495e;
}

.start-menu button {
  padding: 15px 30px;
  margin: 10px;
  font-size: 20px;
  background-color: #2980b9;
  color: #ecf0f1;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.start-menu button:hover {
  background-color: #3498db;
}
