.game-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #2c3e50;
  }
  
  .score {
    font-size: 24px;
    color: #ecf0f1;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  
  .click-target {
    width: 150px;
    transition: transform 0.1s ease;
  }
  
  .click-target:active {
    transform: scale(0.9);
  }
  
  .bonus-item {
    position: absolute;
    top: 20%;
    left: 50%;
    width: 50px;
    cursor: pointer;
    animation: fall 3s ease;
  }
  
  @keyframes fall {
    0% { transform: translateY(-100%); }
    100% { transform: translateY(100%); }
  }
  